@import "normalize";
@import "public";
@import "rem";

$publicPath: '..';

a.popup-close {
    position: absolute;
    right: -40px;
    top: 0;
    background: rgba(255, 255, 255, 0.8);
    height: 30px;
    width: 30px;
    color: rgba(0, 0, 0, 0.8);
    font-size: 32px;
    line-height: 26px;
    text-align: center;
    text-decoration: none;
}

/* 统计代码 */
.website-statistics {
    display: none;
}

a[title='站长统计'] {
    display: none;
}

/* 页面加载 */
.lk-loading {
    position: fixed;
    left: 0;
    top: 0;
    background-color: #fff;
    display: none \9;
    height: 100%;
    width: 100%;
    z-index: 9999;
    opacity: 0;
    transition: all 0.3s linear 0s;

    &.active {
        opacity: 1;
    }

    .lk-loading-center {
        width: 100%;
        height: 100%;
        position: relative;
    }

    .lk-loading-center-absolute {
        margin: (-10/2)+px 0 0 -40px;
        position: absolute;
        left: 50%;
        top: 50%;
        height: 10px;
        width: 100px;

        .round {
            float: left;
            margin: 0 ((100/3 - 15)/2)+px;
            background-color: #046ab6;
            border-radius: 50%;
            height: 10px;
            width: 10px;
        }

        .round-one {
            animation: lk-loading-round 1.5s infinite;
        }

        .round-two {
            animation: lk-loading-round 1.5s infinite;
            animation-delay: 0.25s;
        }

        .round-three {
            animation: lk-loading-round 1.5s infinite;
            animation-delay: 0.5s;
        }
    }
}

@keyframes lk-loading-round {
    75% {
        transform: scale(0);
    }
}

/* 浏览器提示 */
.browser-tips-mask {
    position: fixed;
    left: 0;
    top: 0;
    background: url($publicPath + "/img/opacity-black-bg.png");
    display: block;
    height: 100%;
    width: 100%;
    z-index: 9998;
}

.browser-tips {
    margin-left: -300px;
    position: fixed;
    left: 50%;
    top: 200px;
    background: #fff;
    display: block;
    border-radius: 5px;
    height: 450px;
    width: 600px;
    color: #454545;
    z-index: 9999;

    a.browser-tips-close {
        position: absolute;
        right: 5px;
        top: 5px;
        background: url($publicPath + "/img/close-btn.png") no-repeat center;
        height: 32px;
        width: 32px;
        cursor: pointer;
    }

    h1 {
        margin: 75px auto 50px;
        display: block;
        width: 460px;
        color: #ba5750;
        font-size: 36px;
        text-align: center;
    }

    h3 {
        margin: 20px auto 0;
        width: 460px;
        color: #764b45;
        font-size: 16px;
        line-height: 46px;
    }

    p {
        margin: 50px auto 0;
        width: 600px;
        text-align: center;

        a {
            margin: 0 10px;
            display: inline-block;
            height: 51px;
            width: 148px;
            color: #b74c44;
            font-size: 16px;
            line-height: 51px;
            text-align: center;
        }
    }
}

/* 游戏分页 */
.paging-nav {
    text-align: center;

    a {
        display: inline-block;
        text-align: center;

        &:hover,
        &.active {
            color: #b74c44;
            text-decoration: none;
        }
    }
}

.paging-list {
    li {
        clear: both;
    }

    a {
        float: left;
        color: #000;
    }

    time {
        float: right;
    }
}

.paging-btn {
    text-align: center;

    a {
        display: inline-block;

        &:hover,
        &.active {
            font-weight: bold;
        }
    }
}

/* 视频播放 */
.videoplay-mask {
    position: fixed;
    left: 0;
    top: 0;
    display: none;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 9998;
}

.videoplay-wrap {
    position: fixed;
    top: 30%;
    display: none;
    z-index: 9999;
    width: 650px;
    height: 400px;
    left: 50%;
    margin-left: -325px;

    video {
        width: 650px;
        height: 400px;
    }
}

/* 图片弹出 */
.img-pop {
    position: fixed;
    left: 0;
    top: 0;
    display: none;
    height: 100%;
    width: 100%;
    z-index: 9999;

    .img-mask {
        position: absolute;
        background: rgba(0, 0, 0, 0.8);
        height: 100%;
        width: 100%;
        z-index: 0;
    }

    .img-con-wrap {
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 1;

        .img-con-scroll {
            height: 100%;
            width: 100%;
            overflow-x: hidden;
            overflow-y: auto;

            &::-webkit-scrollbar {
                width: 3px;
                height: 3px;
            }

            &::-webkit-scrollbar-button {
                background-color: #f0f0f0;
            }

            &::-webkit-scrollbar-track {
                background: #f0f0f0;
            }

            &::-webkit-scrollbar-track-piece {
                background: #f0f0f0;
            }

            &::-webkit-scrollbar-thumb {
                background: #bbb;
                border-radius: 5px;
            }

            &::-webkit-scrollbar-corner {
                background: #f0f0f0;
            }
        }

        img {
            display: block;
            width: 100%;
        }
    }
}

.page-mobile {
    a.popup-close {
        right: rem(10);
        top: rem(-50);
        height: rem(40);
        width: rem(40);
        font-size: rem(38);
        line-height: rem(36);
    }

    /* 微信下载提示 */
    .micromsg-tips {
        position: fixed;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, 0.85);
        display: none;
        height: 100%;
        width: 100%;
        z-index: 9999;
    }

    .micromsg-tips-text {
        position: absolute;
        right: 0;
        top: 0;
        background: url($publicPath + "/img/weixinmsg-tips-text.png") no-repeat;
        background-size: 100% auto;
        height: 100%;
        width: 100%;
    }

    /* 视频播放 */
    .videoplay-wrap {
        margin-left: auto;
        position: fixed;
        left: 0;
        top: 30%;
        height: auto;
        width: 100%;

        video {
            height: auto;
            width: 100%;
        }
    }

    /* 图片弹出 */
    .img-pop {
        .img-con-wrap {
            left: 0;
            top: 50%;
            width: 100%;

            .img-con-scroll {
                height: 100%;

                &::-webkit-scrollbar {
                    width: 0;
                    height: 0;
                }

                &::-webkit-scrollbar-thumb {
                    border-radius: 0;
                }
            }
        }
    }
}
